import React from 'react';

export default function Label (props) {
  const {
    FieldName,
    InputValues,
    Periphery
  } = props;

  let value = '';
  if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
    value = InputValues[FieldName];
  }

  return (
    <>
      {Periphery.Title}
      {Periphery.SubTitle}
      {Periphery.TitleComponent}
      <div>{value}</div>
      {Periphery.Note}
      {Periphery.SubNote}
      {Periphery.NoteComponent}
    </>
  );
}
